import React , { Component } from 'react';
import PropTypes from 'prop-types';

const propTypes = {
    index: PropTypes.number.isRequired,
    type: PropTypes.string,
    message: PropTypes.string.isRequired,
    onRemove: PropTypes.func.isRequired
}

const defaultProps = {
    type: 'default'
}

class Message extends Component
{

    componentDidMount()
    {
        const { onRemove, index } = this.props;
        this.timeOut = window.setTimeout(() => onRemove(index), 10000);
    }

    componentWillUnmount()
    {
        window.clearTimeout(this.timeOut);
    }


    render()
    {
        const { type, message, index, onRemove } = this.props;

        return (
            <li className={ `alert alert__${type}` }>
                <div className="alert--info">
                    { message }
                </div>
                <div className="alert--close" onClick={ () => onRemove(index) }>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="-49 141 512 512">
                        <path d="M389.4 515.6L270.8 397l118.6-118.7-63.8-63.7L207 333.2 88.4 214.6l-63.8 63.8L143.2 397 24.6 515.6l63.8 63.8L207 460.7l118.7 118.7"/>
                    </svg>
                </div>
            </li>
        )
    }
}

Message.propTypes = propTypes;
Message.defaultProps = defaultProps;

export default Message;
