import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actions from '../../actions/alert';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';
import { requestGuest as request } from '../../utils/request';
import Wait from '../Wait.jsx';


const mapDispatchToProps = dispatch =>
{
    return {
        actions: bindActionCreators(actions, dispatch)
    }
}

class Forgot extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            wait: false,
            inputs: {
                email: ''
            }
        }
    }

    onChange(event)
    {
        const {name, value} = event.target;

        this.setState(prevState => ({
            ...prevState,
            inputs: {
                ...prevState.inputs,
                [name]: value
            }
        }));
    }

    onSubmit(event)
    {
        event.preventDefault();
        const { inputs, wait } = this.state;
        const { actions, history } = this.props;
        //already request
        if (wait)
            return;
        //check fields
        if (inputs.email.length === 0 )
        {
            actions.error(`Por favor, preencha o campo de ${strings.login.email}`);
            return;
        }
        //set request wait
        this.setState({
            wait: true
        });

        request.post(URLS.API.FORGOT, inputs)
        .then( response => {
            this.setState({
                wait: false
            });
            let { data } = response;
            actions.success(data.message);
            history.push('/');
        })
        .catch( error => {

            this.setState({
                wait: false
            });

            let { data } = error.response;

            if (data.error.message)
                actions.error(`[${data.error.status}] ${data.error.message}`);
            else
                actions.error(strings.error.request);
        });
    }

    render()
    {
        const {inputs, wait} = this.state;
        const {email} = inputs

        return (
            <React.Fragment>
                <form onSubmit={ (event) => this.onSubmit(event) }>
                    <input
                        type='text'
                        placeholder={strings.login.email}
                        name='email'
                        value={ email }
                        onChange={ (event) => this.onChange(event) }
                        autoComplete='off'
                    />
                    { wait ?
                        <Wait />
                    :
                        <button type="submit" className='button success'>{strings.login.bt_forgot}</button>
                    }
                </form>
                <Link to={ URLS.HOME }>
                    {strings.login.link_login}
                </Link>
            </React.Fragment>
        )
    }
}

export default connect(null, mapDispatchToProps)(Forgot);