import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';

const propTypes = {
    data: PropTypes.object.isRequired,
    logOut: PropTypes.func.isRequired
}

function User (props)
{
    const {data, logOut} = props;

    return (
        <div className='header--user'>
            <div className="header--user--me">
                <div className="header--user--me--picture">
                    {data.short_name}
                </div>
                <div className="header--user--me--name">
                    {data.name}
                </div>
                <nav>
                    <ul>
                        <li>
                            <Link to={URLS.ME.CONFIG}>{strings.header.configs}</Link>
                        </li>
                        <li>
                            <Link to={URLS.ME.INFO}>{strings.header.me}</Link>
                        </li>
                        <li>
                            <Link to={URLS.ME.PASSWORD}>{strings.header.change_password}</Link>
                        </li>
                        <li>
                            <button
                                type='button'
                                onClick={ logOut }
                            >
                            {strings.header.logout}
                            </button>
                        </li>
                    </ul>
                </nav>
            </div>

        </div>
    )
}

User.prototype = propTypes;

export default User;