import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Switch, Route } from 'react-router-dom';
import URLS from '../configs/urls';
import _map from 'lodash/map';

import Wrapper from '../components/Contents/Wrapper.jsx';
import Cover from '../components/Contents/Cover.jsx';
import List from '../components/Contents/List.jsx';
import Form from '../components/Contents/Form.jsx';
import Error404 from '../components/Error404.jsx';

const propTypes = {
    routes: PropTypes.array.isRequired
}

class Contents extends Component
{
    constructor(props)
    {
        super(props);
        this.components = {
            Cover: Cover,
            List: List,
            Form: Form
        }
    }

    render()
    {
        const { routes } = this.props;
        const { components } = this;

        return (
            <div className="main--wrapper">
                <Switch>
                    { _map( routes, (route, index) => {
                        return (
                            <Route
                                key={ index }
                                path={ `${URLS.BASE}${route.path}`}
                                render={ props =>
                                    <Wrapper { ...props }>
                                        { React.createElement( components[route.component] ) }
                                    </Wrapper>
                                }
                                exact={true}
                            />
                        )
                    })}

                    <Route component={Error404} />
                </Switch>
            </div>
        )
    }
}

Contents.propTypes = propTypes;

export default Contents;