import React, { Component } from 'react';
import PropTypes from 'prop-types';
import MaskedInput from 'react-text-mask'
import classeNames from 'classnames';

const propTypes = {
    type: PropTypes.string,
    label: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    size: PropTypes.string,
    value: PropTypes.any,
    pattern: PropTypes.string.isRequired,
    onChange: PropTypes.func,
}

const defaultProps = {
    type: 'text',
    size: 'full',
    value: ''
}

class Mask extends Component
{
    constructor(props)
    {
        super(props);
        this.myRef = React.createRef();
    }

    classes()
    {
        const { size } = this.props;

        return classeNames({
            'input': true,
            'input--text': true,
            [size]: true
        });
    }

    setFocus()
    {
        this.myRef.current.focus();
    }

    convertPattern(pattern)
    {
        const patternArray = pattern.split('');

        const newPattern = patternArray.map( item => {
            switch (item)
            {
                case '9':
                    return /\d/; ///[1-9]/
                case 'A':
                    return /[A-Z|a-z]/;
                default:
                    return item;
            }
        })

        return newPattern;
    }

    onBlur()
    {
        const { name, pattern, value, onChange } = this.props;
        const cleanValue = value.replace('_', '');

        if (pattern.length === cleanValue.length )
            return;

        onChange({
            target: {
                name: name,
                value: ''
            }
        });
    }

    render()
    {
        const { value, onChange, label, component, size, pattern, ...props } = this.props;

        return (
            <div
                className={ this.classes() }
            >
                <label>{ label }</label>
                <div className="group">
                    <MaskedInput
                        mask={ this.convertPattern(pattern) }
                        value={ value }
                        keepCharPositions={ true }
                        // placeholderChar='\u2000'
                        { ...props }
                        onChange={ (event) => onChange(event) }
                        onBlur={ () => this.onBlur() }
                    />
                </div>
            </div>
        )
    }
}

Mask.propTypes = propTypes;
Mask.defaultProps = defaultProps;

export default Mask;