import React from 'react';
import PropTypes from 'prop-types';
import Select from '../Inputs/Select.jsx';

const propTypes = {
    options: PropTypes.array.isRequired,
    type: PropTypes.any,
    order: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    handleFilter: PropTypes.func.isRequired,
}

const orderOptions = [
    {
        value: 'asc',
        label: 'ASC',
    },
    {
        value: 'desc',
        label: 'DESC',
    }
]

function Sort (props)
{
    const { options, type, order, onChange, handleFilter } = props;

    return (
        <div className="list--filters">
            <Select
                options={ options }
                name='sort_type'
                value={ type }
                onChange={ onChange }
            />
            <Select
                options={ orderOptions }
                name='sort_order'
                value={ order }
                onChange={ onChange }
            />
            <button
                type="button"
                className="list--button"
                onClick={ handleFilter }
            >
                <img src="/gerador/images/admin/filter.svg" />
            </button>
        </div>
    )
}

Sort.propTypes  = propTypes;

export default Sort;