export default (url, obj) =>
{
	let parsed = url;
	const regexp = new RegExp(/\:(\w+)/, 'ig');
	const params = url.match(regexp).filter( (value, index, self) => {
		return self.indexOf(value) === index;
	});

	params.forEach(param => {
		const key = param.replace(':', '');
		if (obj[key])
			parsed = parsed.replace(new RegExp(param, 'g'), obj[key]);
	});

	return parsed;
}