import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actionsAlert from '../../actions/alert';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';
import { request } from '../../utils/request';
import _isEmpty from 'lodash/isEmpty';
import _mapValues from 'lodash/mapValues';
import queryString from 'query-string';
import parseURL from '../../utils/parseUrl';

import Title from '../Title.jsx';
import Buttons from '../List/Buttons.jsx';
import Sort from '../List/Sort.jsx';
import Search from '../List/Search.jsx';
import Pagination from '../List/Pagination.jsx';
import Table from '../List/Table.jsx';
import Wait from '../Wait.jsx';

const mapDispatchToProps = dispatch =>
{
    return {
        actions: bindActionCreators(actionsAlert, dispatch),
    }
}

const propTypes = {
    data: PropTypes.object
}

const defaultProps = {
    data: {}
}

class List extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            wait: true,
            header: null,
            data: [],
            pagination: null,
            filters: {
                sort_type: null,
                sort_order: 'asc',
                search_type: null,
                search_query: '',
            }
        }
    }

    componentDidMount()
    {
        const { search } = this.props.location;
        const parsed = queryString.parse(search);
        const { filters } = this.state;

        const newFilters = _mapValues( filters, (item, key) => {
            if (parsed[key])
                return parsed[key];
            return item;
        })

        this.setState({
            filters: newFilters
        });

        this.fetchData(parsed);
    }

    fetchData(params)
    {
        const { data: url } = this.props.data;
        const requestUrl = URLS.API.BASE + url;
        const { match, history } = this.props;

        history.push(`${match.path}?${queryString.stringify(params)}`);

        this.setState({
            wait: true
        });

        request.get(requestUrl, {
            params: params
        })
        .then(response => {
            const { data } = response;
            this.setState(prevState => ({
                ...prevState,
                wait: false,
                header: data.header,
                paths: data.paths,
                data: data.data,
                pagination: data.pagination,
            }));
        })
        .catch( error => {
            let { data } = error.response;

            if (data.error.message)
                actions.error(`[${data.error.status}] ${data.error.message}`);
            else
                actions.error(strings.error.request);
        });
    }

    onChange(event)
    {
        const { name, value } = event.target;

        this.setState(prevState => ({
            ...prevState,
            filters: {
                ...prevState.filters,
                [name]: value
            }
        }));
    }

    handleFilter()
    {
        const { wait } = this.state;

        if (wait)
            return;

        const params = this.cleanFilters();

        if (_isEmpty(params))
            return;

        this.fetchData(params)
    }

    cleanFilters()
    {
        const { filters } = this.state;

        const params = {}

        if (filters.sort_type)
        {
            params.sort_type = filters.sort_type
            params.sort_order = filters.sort_order
        }

        if (filters.search_type)
        {
            params.search_type = filters.search_type
            params.search_query = filters.search_query
        }

        return params;
    }

    handlePage(page)
    {
        const params = this.cleanFilters();
        params.page = page;

        this.fetchData(params);
    }

    onSwitch(index)
    {
        const { data: dataState } = this.state;
        const { actions } = this.props;
        const { status: url } = this.props.data.paths;

        const parsedURL =  parseURL(url, dataState[index]);

        request.post(URLS.API.BASE+parsedURL)
        .then(resp => {
            const newData = [...dataState];
            const { data: dataResp } = resp;
            newData[index].status = dataResp.data.status

            this.setState(prevProps => ({
                ...prevProps,
                data: newData
            }));

            actions.success(dataResp.message);
        })
        .catch( err => {
            const { data: dataErr } = err.response;

            if (dataErr.error)
                actions.error(`[${dataErr.error.status}] ${dataErr.error.message}`);
            else
                actions.error(strings.error.request);
        });
    }

    onEdit(index)
    {
        const { data: dataState } = this.state;
        const { data: dataProps, history } = this.props;
        const obj = dataState[index];
        const { edit: url } = dataProps.paths;

        const parsedURL =  parseURL(url, obj);

        history.push(URLS.BASE+parsedURL);
    }

    onDelete(index)
    {
        const { data: dataState, filters } = this.state;
        const { actions, data: dataProps } = this.props;
        const obj = dataState[index];
        const { delete: url } = dataProps.paths;

        const parsedURL =  parseURL(url, obj);

        request.delete(URLS.API.BASE+parsedURL)
        .then(resp => {
            const { data: dataResp } = resp.data;
            actions.success(dataResp.message);
            this.fetchData(filters);
        })
        .catch( err => {
            const { data: dataErr } = err.response;

            if (dataErr.error)
                actions.error(`[${dataErr.error.status}] ${dataErr.error.message}`);
            else
                actions.error(strings.error.request);
        });
    }

    render()
    {
        const { title, actions, filters, paths } = this.props.data;
        const { filters: filters_state, header, data, pagination, wait } = this.state;

        return (
            <React.Fragment>
                <Title value={ title } />
                <div className="list">
                    <header>
                        { actions ?
                            <Buttons data={ actions } />
                        : null }
                        { filters && filters.sort ?
                            <Sort
                                options={ filters.sort }
                                type={ filters_state.sort_type }
                                order={ filters_state.sort_order }
                                onChange={ (event) => this.onChange(event) }
                                handleFilter={ () => this.handleFilter() }
                            />
                        : null }
                        { filters && filters.search ?
                            <Search
                                options={ filters.search }
                                type={ filters_state.search_type }
                                query={ filters_state.search_query }
                                onChange={ (event) => this.onChange(event) }
                                handleFilter={ () => this.handleFilter() }
                            />
                        :null }
                    </header>
                    { wait ?
                        <Wait />
                    :
                        <Table
                            header={ header }
                            data={ data }
                            onSwitch={ paths.status ? (index) => this.onSwitch(index) : null }
                            onDelete={ paths.delete ? (index) => this.onDelete(index) : null }
                            onEdit={ paths.edit ? (index) => this.onEdit(index) : null }
                        />
                    }
                    { pagination ?
                        <div className="list--nav">
                            <Pagination
                                data={ pagination }
                                onClick={ (page) => this.handlePage(page) }
                            />
                        </div>
                    : null }
                </div>
            </React.Fragment>
        )
    }
}

List.propTypes = propTypes;
List.defaultProps = defaultProps;

export default withRouter(connect(null, mapDispatchToProps)(List));