import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import URLS from '../../configs/urls';

const propTypes = {
    data: PropTypes.array.isRequired,
    filter: PropTypes.string,
    pathname: PropTypes.string.isRequired
}

const defaultProps = {
    filter: ''
}

class Menu extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            opened: null
        }
    }

    getMenuGroup(items)
    {
        return (
            <ul>
                { items.map(item => {
                    if (item.children)
                        return this.getSubMenu(item)
                    else
                        return this.getLink(item)
                })}
            </ul>
        )
    }

    getLink(item)
    {
        return (
            <li key={item.id}>
                <NavLink
                    to={ `${URLS.HOME}${item.path}` }
                    exact={ item.exact || false }
                >
                    {item.label}
                </NavLink>
            </li>
        )
    }

    getSubMenu(sub)
    {
        return (
            <li key={sub.id}
                className={ this.classes(`${URLS.HOME}${sub.path}`) }
            >
                <h4 onClick={ () => this.toogle(`${URLS.HOME}${sub.path}`) } >
                    {sub.label}
                </h4>
                <ul className="sidebar--nav--sub">
                    { sub.children.map( item => {
                        return this.getLink(item)
                    })}
                </ul>
            </li>
        )
    }

    toogle(path)
    {
        const { opened } = this.state;
        const value = opened === path ? null : path;

        this.setState({
            opened: value
        });
    }

    classes(path)
    {
        const { opened } = this.state;
        const { pathname, filter } = this.props;

        return classNames({
            'open' : opened === path || filter !== '',
            'active' : pathname.startsWith(path)
        });
    }

    render()
    {
        const { data } = this.props;

        return (
            <nav className="sidebar--nav">
                { data.map((item, index) => {
                    return (
                        <React.Fragment key={index} >
                            <h3>{item.group}</h3>
                            { this.getMenuGroup(item.routes) }
                        </React.Fragment>
                    )
                })}
            </nav>
        )
    }
}

Menu.propTypes = propTypes;
Menu.defaultProps = defaultProps;

export default Menu;