import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _filter from 'lodash/filter';
import { request } from '../../utils/request';

const propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.any,
    options: PropTypes.any,
    param: PropTypes.any,
}

const defaultProps = {
    value: null,
    param: undefined
}

class Select extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            opened: false,
            query: '',
            options: []
        }
        this.myRef = React.createRef();
        this.clickOutside = this.clickOutside.bind(this);
    }

    componentDidMount()
    {
        document.addEventListener('click', this.clickOutside);
        this.setOptions();
    }

    componentDidUpdate(prevProps)
    {
        const { options, param } = this.props;
        const { options: prevOptions, param: prevParam } = prevProps;

        if (options !== prevOptions || param !== prevParam)
            this.setOptions();

        if (param !== prevParam)
            this.onClick('');
    }

    componentWillMount()
    {
        document.removeEventListener('click', this.clickOutside);
    }

    setOptions()
    {
        const { options } = this.props;

        if (typeof options === 'string')
            this.fetchOptions();
        else
        {
            this.setState({
                options: options
            });
        }

    }

    fetchOptions()
    {
        const { options, param } = this.props;

        if (param === '')
            return;

        request.get(options, {
            params: {
                field: param
            }
        })
        .then(resp => {
            this.setState({
                options: resp.data
            });
        })
        .catch( err => {
            console.log(err.response);
        })
    }

    toogle()
    {
        const { opened } = this.state;
        this.setState({
            opened: !opened
        });
    }

    clickOutside(event)
    {
        const { target } = event;
        const { current } = this.myRef;

        if (current && !current.contains(target))
            this.setState(prevState => ({
                ...prevState,
                opened: false,
                query: '',
            }))
    }

    onChange(event)
    {
        const { name, value } = event.target;
        this.setState({
            [name]: value
        })
    }

    onClick(value)
    {
        const { onChange, name } = this.props;
        onChange({
            target: {
                name: name,
                value: value
            }
        })

        this.setState({
            query: ''
        })
    }

    getSelected()
    {
        const { value } = this.props;
        const { options } = this.state;
        const item = _filter(options, { value: value })
        return item.length ? item[0].label : 'Selecione';
    }

    getOptions()
    {
        const { query, options } = this.state;

        if (!query)
            return options;

        return options.filter(item => {
            return item.label
                        .toLowerCase()
                        .indexOf(query.toLowerCase()) !== -1;
        })
    }

    render()
    {
        const { opened, query } = this.state;

        return (
            <div
                className="input--select"
                onClick={ () => this.toogle() }
                ref={ this.myRef }
            >
                <div className="input--select--wrapper">
                    <div className="input--select--selected">
                        { opened ? null : this.getSelected() }
                    </div>
                    <input
                        type="text"
                        className="input--select--input"
                        name='query'
                        value={ opened ? query : '' }
                        autoComplete="off"
                        onChange={ (event) => this.onChange(event) }
                    />
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" className="input--select--arrow">
                        <path d="M5 3l3.1-3L20 12 8.1 24 5 21l9-9-9-9z"/>
                    </svg>
                </div>
                { opened ?
                <div className="input--select--options">
                    { this.getOptions().map((item, key) => {
                        return (
                            <div
                                key={key}
                                className="input--select--option"
                                onClick={ () => this.onClick(item.value) }
                            >
                                {item.label}
                            </div>
                        )
                    })}
                </div>
                : null }
            </div>
        )
    }
}

Select.propTypes = propTypes;
Select.defaultProps = defaultProps;

export default Select;