import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actionsAlert from '../../actions/alert';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';
import { request } from '../../utils/request';

import Wait from '../Wait.jsx';
import Error404 from '../Error404.jsx';

const mapDispatchToProps = dispatch  =>
{
    return {
        actions: {
            alert: bindActionCreators(actionsAlert, dispatch)
        }
    }
}

class Wrapper extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            wait: false,
            data: null
        }
    }

    componentDidMount()
    {
        this.fetchData();
    }

    componentDidUpdate(prevProps)
    {
        const { pathname } = this.props.location;
        const { pathname: prevPathName } = prevProps.location;

        if (pathname !== prevPathName)
            this.fetchData();
    }

    fetchData()
    {
        const { wait } = this.state;
        const { location, actions } = this.props;
        const prefix = `${URLS.API.BASE}/page`
        const apiUrl = location.pathname.replace(URLS.BASE, prefix);

        if (wait)
            return;

        this.setState({
            wait: true
        });

        request.get(apiUrl)
        .then( response => {
            let { data } = response;
            this.setState({
                wait: false,
                data: data
            });
        })
        .catch( err => {

            this.setState({
                wait: false,
                data: null
            });

            const { data: dataErr } = err.response;

            if (dataErr.error)
                actions.alert.error(`[${dataErr.error.status}] ${dataErr.error.message}`);
            else
                actions.alert.error(strings.error.request);
        })
    }

    render()
    {
        const { wait, data } = this.state;

        if ( wait )
            return <Wait />

        if (!data)
            return <Error404 />

        return (
            React.cloneElement(
                this.props.children,
                {
                    data: data
                }
            )
        )
    }

}

export default connect(null, mapDispatchToProps)(Wrapper);