import React, {Component} from 'react';
import PropTypes from 'prop-types';

import Brand from '../components/Brand.jsx';
import Projects from '../components/Sidebar/Projects.jsx';
import Search from '../components/Sidebar/Search.jsx';
import Menu from '../components/Sidebar/Menu.jsx';
import LogOut from '../components/Sidebar/LogOut.jsx';

const propTypes = {
    menu: PropTypes.array.isRequired,
    name: PropTypes.string.isRequired,
    pathname: PropTypes.string.isRequired,
    logOut: PropTypes.func.isRequired
}

class Sidebar extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            query: '',
        };
    }

    handleSearch(event)
    {
        const { value } = event.target;
        const query = value.toLowerCase();
        const { menu } = this.props;
        const filtered = this.filterMenu(menu, query);

        this.setState({
            query : query,
            filtered: filtered
        });
    }

    filtered()
    {
        const { menu } = this.props;
        const { query } = this.state;

        return this.filterMenu(menu, query);
    }

    filterMenu(items, query)
    {
        if (query.length === 0)
            return items;

        return items.map(item => {
            return {
                group: item.group,
                routes: this.filterChildren(item.routes, query)
            }
        });
    }

    filterChildren(routes, query)
    {
        return routes.filter( item => {
            if (item.children)
            {
                const sub = this.filterChildren(item.children, query);

                if (sub.length > 0)
                    return true;
            }

            return item.label
                        .toLowerCase()
                        .indexOf(query.toLowerCase()) !== -1;
        });
    }

    render()
    {
        const { query } = this.state;
        const { name , pathname, logOut} = this.props;

        return (
            <aside className='sidebar'>
                <Brand />
                <Projects name={ name } />
                <Search
                    onChange={ (event) => this.handleSearch(event) }
                    value={ query }
                />
                <Menu
                    data={ this.filtered() }
                    filter={ query }
                    pathname={ pathname }
                />
                <LogOut onClick={ logOut } />
            </aside>
        )
    }
}

Sidebar.propTypes = propTypes;

export default Sidebar;