import React from 'react';
import PropTypes from 'prop-types';

const propTypes = {
    name: PropTypes.string.isRequired
}

function Projects (props)
{
    const { name } = props;

    return (
        <div className="sidebar--projects">
            <h3>{name}</h3>
        </div>
    )
}

Projects.propTypes = propTypes;

export default Projects;