import _flatMapDeep from 'lodash/flatMapDeep';

import * as actionTypes from '../actiontypes/alert';
import store from '../stores/alert';

export default (state = store, action) =>
{
    switch(action.type)
    {
        case actionTypes.ADD_MESSAGE:
            const { data, color } = action;
            let { last } = state;
            //put in array if not
            const array = typeof data === "object" ? data : [data]
            const flatArray = _flatMapDeep(array, element => element );
            //format messages
            const newMessages = flatArray.map(element => {
                return {
                    id: last++,
                    type: color,
                    message: element
                }
            });
            //return new state
            return {
                ...state,
                last: last,
                messages: [
                    ...state.messages,
                    ...newMessages
                ]
            };
        case actionTypes.REMOVE_MESSAGE:
            const { index } = action;
            const { messages } = state;
            //remove the message
            const messagesList = [
                ...messages.slice(0, index),
                ...messages.slice(index + 1)
            ];
            //return new state
            return {
                ...state,
                messages: messagesList
            };

        default:
            return state;
    }
};