import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import _map from 'lodash/map';
import URLS from '../../configs/urls';


const propTypes = {
    data: PropTypes.array.isRequired
}

class Buttons extends Component
{
    getInsideLink(item, index)
    {
        const className = item.class || 'primary';

        return (
            <Link
                key={index}
                to={URLS.BASE+item.path}
                className={`button ${className}`}
            >
                {item.label}
            </Link>
        )
    }

    getOutSideLink(item, index)
    {
        const className = item.class || 'primary';

        return (
            <a
                key={index}
                href={URLS.BASE+item.url}
                target='_blank'
                className={`button ${className}`}
            >
                {item.label}
            </a>
        )
    }

    render()
    {
        const { data } = this.props;

        return (
            <div className="list--buttons">
                { _map (data, (item, index) => {
                    if (item.url)
                        return this.getOutSideLink(item, index);

                    return this.getInsideLink(item, index);
                })}
            </div>
        )
    }
}

Buttons.propTypes = propTypes;

export default Buttons;