import React, {Component} from 'react';
import { withRouter, Switch, Route } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actionsAlert from '../actions/alert';
import * as actionsUser from '../actions/user';

import URLS from '../configs/urls';
import strings from '../configs/strings';
import { request } from '../utils/request';

import Wait from '../components/Wait.jsx';
import Sidebar from './Sidebar.jsx';
import Header from '../components/Header/Main.jsx';
import Contents from './Contents.jsx';

const mapDispatchToProps = dispatch =>
{
    return {
        actions: {
            alert: bindActionCreators(actionsAlert, dispatch),
            user: bindActionCreators(actionsUser, dispatch)
        }
    }
}

const mapStateToProps = state =>
{
    return {
        user: state.user,
    }
}

class App extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            wait: true,
            name: null,
            routes: [],
            menu: []
        }
    }

    componentDidMount()
    {
        const { actions } = this.props;

        request.get(URLS.API.MAIN)
        .then( response => {
            const { data } = response;
            this.setState({
                wait: false,
                name: data.name,
                menu: data.menu,
                routes: this.getRoutes(data.routes, data.menu)
            });
        })
        .catch( err => {
            const { data: dataErr } = err.response;

            if (dataErr.error)
                actions.alert.error(`[${dataErr.error.status}] ${dataErr.error.message}`);
            else
                actions.alert.error(strings.error.request);
        });
    }

    handleLogOut()
    {
        const { actions } = this.props;
        actions.user.signOut();
    }

    getRoutes(dataRoutes, dataMenu)
    {
        let routesArray = [];

        dataMenu.map( group => {
            group.routes.map( route => {
                if (route.children)
                    route.children.map( children => {
                        routesArray.push({
                            path: children.path,
                            component: children.component || null,
                        });
                    });

                routesArray.push({
                    path: route.path,
                    component: route.component || null,
                });
            });
        });

        dataRoutes.map( route => {
            routesArray.push({
                path: route.path,
                component: route.component || null,
            });
        })

        return routesArray;
    }

    render()
    {
        const { wait, routes, menu, name } = this.state;
        const { user, location } = this.props;

        if (wait)
            return (
                <div id='app'>
                    <Wait />
                </div>
            )

        return (
            <div id='app'>
                <Sidebar
                    name={ name }
                    menu={ menu }
                    pathname={ location.pathname }
                    logOut={ () => this.handleLogOut() }
                />
                <div className='main'>
                    <Header
                        me={ user }
                        logOut={ () => this.handleLogOut() }
                    />
                    <Contents routes={ routes } />
                </div>
            </div>
        )
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));