import React, {Component} from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actions from '../actions/user';
import URLS from '../configs/urls';
import { requestGuest as request } from '../utils/request';

import Alert from './Alert.jsx';
import Wait from '../components/Wait.jsx';
import Auth from './Auth.jsx';
import App from './App.jsx';

const mapDispatchToProps = dispatch =>
{
    return {
        actions: bindActionCreators(actions, dispatch)
    }
}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

class Main extends Component
{

    constructor(props)
    {
        super(props);
        this.state = {
            wait: false
        }
    }

    componentDidMount()
    {
        const { user } = this.props;
        const access_token = window.localStorage.getItem('access_token');
        if (!user.is_logged && access_token)
            this.getLoggedUser(access_token);
    }

    getLoggedUser(token)
    {
        const { actions } = this.props;

        this.setState({
            wait: true
        });
        request.get(URLS.API.AUTH_STATUS,{
            params:{
                token: token
            }
        })
        .then( response => {
            this.setState({
                wait: false
            });
            let { data } = response;
            actions.signIn({
                name: data.user.name,
                short_name: data.user.short_name,
                access_token: data.user.access_token
            });
        })
        .catch(error => {
            this.setState({
                wait: false
            });
        });
    }

    render()
    {
        const { user } = this.props;
        const { wait } = this.state;

        if (wait)
            return <Wait />;

        return (
            <React.Fragment>
                <Alert />
                <Router>
                    { user.is_logged === false || user.access_token === null ?
                        <Auth />
                        :
                        <App />
                    }
                </Router>
            </React.Fragment>
        )
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Main);