import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actionsAlert from '../../actions/alert';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';
import { request } from '../../utils/request';

import Title from '../Title.jsx';
import Buttons from '../Form/Buttons.jsx';
import Text from '../Inputs/Text.jsx';
import Mask from '../Inputs/Mask.jsx';
import TextArea from '../Inputs/TextArea.jsx';
import Select from '../Inputs/SelectInput.jsx';
import Wait from '../Wait.jsx';

const mapDispatchToProps = dispatch =>
{
    return {
        actions: bindActionCreators(actionsAlert, dispatch),
    }
}

const propTypes = {
    data: PropTypes.object
}

const defaultProps = {
    data: {}
}

const types = {
    Text: Text,
    TextArea: TextArea,
    Select: Select,
    Mask: Mask,
}

class Form extends Component
{
    constructor(props)
    {
        super(props);

        const{ data } = props;

        this.state = {
            wait: false,
            title: data.title,
            actions: data.actions,
            data: data.data,
            form: data.form
        }
    }

    getComponents(group)
    {
        const { data } = this.state;

        return (
            <div className="form--group--fields">
                { group.map((item, index) => {
                    const props = {
                            key: index,
                            value: data[item.name],
                            ...item,
                            onChange: (event) => this.onChange(event)
                    }

                    if (item.param)
                        props.param = data[item.param] || '';

                    return (
                        React.createElement(types[item.component], props)
                    )
                })}
            </div>
        )
    }

    onChange(event)
    {
        const { name, value } = event.target;
        this.setState( prevState => ({
            ...prevState,
            data: {
                ...prevState.data,
                [name]: value
            }
        }));
    }

    onSave()
    {
        const { actions, history } = this.props;
        const { save: saveURL } = this.state.actions;
        const { wait, data } = this.state;

        if (wait)
            return;

        request.post(URLS.API.BASE + saveURL, data)
        .then(resp => {
            this.setState(prevState => ({
                ...prevState,
                wait: false,
                data: resp.data.data
            }));

            if (resp.data.redirect)
                history.push(URLS.BASE + resp.data.redirect);

            actions.success(resp.data.message);
        })
        .catch(err => {
            let { data } = err.response;

            if (data.error.message)
                actions.alert.error(`[${data.error.status}] ${data.error.message}`);
            else
                actions.alert.error(strings.error.request);
        });
    }

    onDelete()
    {
        const { actions, history } = this.props;
        const { delete: deleteURL } = this.state.actions;
        const { wait, data } = this.state;

        if (wait)
            return;

        request.delete(URLS.API.BASE + deleteURL)
        .then(resp => {
            this.setState(prevState => ({
                ...prevState,
                wait: false,
            }));

            history.push(URLS.BASE + resp.data.redirect);

            actions.success(resp.data.message);
        })
        .catch(err => {
            let { data } = err.response;

            if (data.error.message)
                actions.alert.error(`[${data.error.status}] ${data.error.message}`);
            else
                actions.alert.error(strings.error.request);
        });
    }

    onBack()
    {
        const { history } = this.props;
        const { back: backUrl } = this.state.actions;

        history.push(URLS.BASE + backUrl);
    }

    render()
    {
        const { title, actions, form, wait } = this.state;
        return (
            <React.Fragment>
                <Title value={ title } />
                <div className="form">
                    <header>
                        <Buttons
                            data={ actions }
                            onSave={ () => this.onSave() }
                            onDelete={ () => this.onDelete() }
                            onBack={ () => this.onBack() }
                        />
                    </header>
                    { wait ? <Wait /> : null }
                    <form>
                        { form.map( ( group, index ) => {
                            return (
                                <div className="form--group" key={ index }>
                                    <div className="form--group--info">
                                        <h3>{ group.name }</h3>
                                        <p>{ group.content }</p>
                                    </div>
                                    { this.getComponents(group.components) }
                                </div>
                            )
                        })}
                    </form>
                    <footer>
                        <Buttons
                            data={{ save: actions.save }}
                            onSave={ () => this.onSave() }
                        />
                    </footer>
                </div>
            </React.Fragment>
        )
    }
}

Form.propTypes = propTypes;
Form.defaultProps = defaultProps;

export default withRouter(connect(null, mapDispatchToProps)(Form));