import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import * as actionsAlert from '../../actions/alert';
import * as actionsUser from '../../actions/user';
import URLS from '../../configs/urls';
import strings from '../../configs/strings';
import { requestGuest as request } from '../../utils/request';
import Wait from '../Wait.jsx';

const mapDispatchToProps = dispatch =>
{
    return {
        actions: {
            alert: bindActionCreators(actionsAlert, dispatch),
            user: bindActionCreators(actionsUser, dispatch)
        }
    }
}

class Login extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            wait: false,
            inputs: {
                username: '',
                password: ''
            }
        }
    }

    onChange(event)
    {
        const {name, value} = event.target;

        this.setState(prevState => ({
            ...prevState,
            inputs: {
                ...prevState.inputs,
                [name]: value
            }
        }));
    }

    onSubmit(event)
    {
        event.preventDefault();
        const { inputs, wait } = this.state;
        const { actions } = this.props;
        //already request
        if (wait)
            return;
        //check fields
        if (inputs.username.length === 0 || inputs.password.length === 0)
        {
            actions.alert.error(`Por favor, preencha o campo de ${strings.login.username} e ${strings.login.password}`);
            return;
        }
        //set request wait
        this.setState({
            wait: true
        });

        request.post(URLS.API.LOGIN, inputs)
        .then( response => {
            this.setState({
                wait: false
            });
            let { data } = response;
            actions.user.signIn({
                name: data.user.name,
                short_name: data.user.short_name,
                access_token: data.user.access_token
            });
        })
        .catch( error => {

            this.setState({
                wait: false
            });

            let { data } = error.response;

            if (data.error.message)
                actions.alert.error(`[${data.error.status}] ${data.error.message}`);
            else
                actions.alert.error(strings.error.request);
        });
    }

    render()
    {
        const { inputs, wait } = this.state;
        const { username, password } = inputs;
        return (
            <React.Fragment>
                <form onSubmit={ (event) => this.onSubmit(event) }>
                    <input
                        type='text'
                        placeholder={strings.login.username}
                        name='username'
                        value={ username }
                        onChange={ (event) => this.onChange(event) }
                        autoComplete='off'
                    />
                    <input
                        type='password'
                        placeholder={strings.login.password}
                        name='password'
                        value={ password }
                        onChange={ (event) => this.onChange(event) }
                        autoComplete='off'
                    />
                    { wait ?
                        <Wait />
                    :
                        <button type="submit" className='button success'>{strings.login.bt_login}</button>
                    }
                </form>
                <Link to={ URLS.LOGIN.FORGOT }>
                    {strings.login.link_forgot}
                </Link>
            </React.Fragment>
        )
    }
}

export default connect(null, mapDispatchToProps)(Login);