import * as actionTypes from '../actiontypes/user';
import store from '../stores/user';

export default (state = store , action) =>
{
    switch(action.type)
    {
        case actionTypes.SIGNIN_USER:
            window.localStorage.setItem('access_token', action.data.access_token);
            return {
                ...state,
                is_logged: true,
                name: action.data.name,
			    short_name: action.data.short_name,
			    access_token: action.data.access_token
            };
        case actionTypes.SIGNOUT_USER:
            window.localStorage.removeItem('access_token');
            return {
                ...state,
                is_logged: false,
                name: null,
			    short_name: null,
			    access_token: null
            };
        default:
            return state;
    }
};