import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Closed from './Actions/Closed.jsx';
import Opened from './Actions/Opened.jsx';
import ConfirmDelete from './Actions/ConfirmDelete.jsx';

const propTypes = {
    onDelete: PropTypes.func,
    onEdit: PropTypes.func,
    index: PropTypes.number
}

class Actions extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            status: 'closed'
        }
    }

    changeStatus( value )
    {
        this.setState({ status: value });
    }

    getComponent()
    {
        const { status } = this.state;
        const { onDelete, onEdit } = this.props;

        switch(status)
        {
            case 'opened':
                return <Opened
                    onDelete={ onDelete ? () => this.handleDelete() : null }
                    onEdit={ onEdit ? () => this.handleEdit() : null }
                />
            case 'confirm_delete':
                return <ConfirmDelete />
            default:
                return <Closed onClick={ () => this.changeStatus('opened') } />
        }
    }

    handleEdit()
    {
        const { onEdit, index } = this.props;

        onEdit(index);
    }

    handleDelete()
    {
        const { status } = this.state;
        const { onDelete, index } = this.props;

        // if ( status !== 'confirm_delete')
        // {
        //     this.changeStatus('confirm_delete');
        //     return;
        // }

        onDelete(index);
    }

    render()
    {
        return (
            <div className='list--actions--container'
                onMouseLeave={ () => this.changeStatus('closed')  }
            >
                { this.getComponent() }
            </div>
        )
    }

}

Actions.propTypes = propTypes;

export default Actions;